* {
	box-sizing: border-box;
	font-family: 'Inter', sans-serif;
}

body,
html {
	padding: 0;
	margin: 0;
}

body {
	background-color: #23252c;
}

#root {
	max-width: 400px;
	margin: 0 auto;
	color: white;
	background: #1a1b21;
	border-radius: 0 0 1em 1em;
}

header {
	text-align: center;
	letter-spacing: 1px;
}

.header-name {
	margin-bottom: 0.2em;
}

.header-sub-name {
	color: #f3bf99;
	font-weight: 400;
	margin: 0;
	margin-bottom: 0.2em;
}

.header-link {
	transition: all 500ms;
}

.header-link,
.header-link:visited {
	text-decoration: none;
	color: white;
}

.header-link:focus,
.header-link:hover {
	color: gray;
}

.header-link:active {
	color: #f3bf99;
}

.header-image {
	margin-top: 1em;
	border-radius: 1em;
}

.btn-wrapper {
	margin-top: 1em;
	display: flex;
	justify-content: space-evenly;
	align-items: center;
}

.btn {
	display: flex;
	justify-content: center;
	align-items: center;
	width: 150px;
	cursor: pointer;
	font-weight: 500;
	border-radius: 1em;
	font-size: 1rem;
	background-color: #d1d5db;
	text-decoration: none;
	color: #23252c;
}

.email-btn:visited {
	text-decoration: none;
	color: white;
}

.btn img {
	margin-right: 0.5em;
}

main {
	padding: 0 2em 1em;
}

.linkedin-btn {
	background: #5093e2;
	color: white;
}

.footer-section {
	padding: 1em;
	background: #161619;
	display: flex;
	flex-direction: column;
	justify-content: center;
	border-radius: 0 0 1em 1em;
}

.footer-link {
	display: flex;
	justify-content: center;
	align-items: center;
}

.main-title {
	margin: 0.8em 0;
}

.main-title:first-of-type {
	margin-top: 1.5em;
}

.main-description {
	margin: 0;
	line-height: 1.4;
}
